import { Injectable } from '@angular/core';

declare var $:any;

@Injectable()
export class NotificationsService {

  constructor() { }

    showNotification(msg, color, icon) {

        $.notify({
            icon: 'notifications',
            message: msg,

        }, {
            type: color,
            timer: 3000,
            placement: {
                from: 'bottom',
                align: 'right'
            }
        });
    }
}




